/* step */
.step{
    $primary: $teal;
    //--
    display: flex;
    justify-content: center;
    padding-left: 0;
    padding-bottom: calc(18px * 2);
    margin-block: 46px;
    list-style: none;

    &-item{
        flex-basis: 180px;
        display: flex;       
        
        
        // Line
        &::after{
            position: relative;
            z-index: 1;
            margin-right: -7px;
        }
        &::before,
        &::after{
            content: '';
            display: block;
            flex-grow: 1;
            height: 5px;
            background-color: $disabled;
            transform: translateY(19px);
            @include breakpoint($lg){
                height: 8px;
            }
        }
        // 頭尾line隱藏
        &:first-child::before,
        &:last-child::after{
            background-color: transparent !important;
        }

        // 狀態 finished
        &.finished{
            .order{
                color: white;
                background-color: $primary;
            }
        }
        // 狀態 active
        &.active{
            .order{
                color: $primary;
                background-color: white;
            }
            // Line
            &::after{
                border-radius: 0 999rem 999rem 0;
            }
        }
        // 狀態 共通
        &.finished,
        &.active{            
            .order{
                border-color: $primary;
            }
            .text{
                color: black;
            }
            // Line
            &::before,
            &::after{
                background-color: $primary;
            }
        }
    }
    &-link{
        position: relative;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 16px;
        color: grey;
        text-decoration: none;        
    }

    .order{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        font-size: 1.2rem;
        border: 4px solid $disabled;
        background-color: $disabled;
        border-radius: 999rem;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        @include breakpoint($lg){
            width: 46px;
        height: 46px;
        font-size: 1.5rem; //24px
        border-width: 6px;
        }
    }
    .text{
        position: absolute;
        bottom: 0;
        transform: translateY(150%);
        font-size: 1rem;
        font-weight: 500;
        white-space: nowrap;
        @include breakpoint($lg){
            font-size: 1.125rem; //18px
        }
    }
}