/* file */
.file{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    padding: 6px 16px;
    background: #FCFBFB;
    border: 1px solid #E4E7EE;
    border-radius: 10px;
    font-size: 1.125rem; //18px

    > .start{
        display: flex;
        align-items: center;
        gap: 12px;
    }
}