/* tag */
.tag{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 74px;
    height: 25px;

    color: var(--text);
    background-color: var(--bg);
    // font-size: 17px; 
    line-height: 1;
    
    &.warning{
        $orange: #F97C0E;
        --text: #{$orange};
        --bg: #{rgba($orange, .15)};
    }
    &.info{
        // --text: #{$teal};
        // --bg: #{rgba($teal, .15)};
        --bg: #C4E2FF;
    }
}