/* nav-tabs */
.nav-tabs-wrapper{
    > .nav-tabs{
        --bs-nav-tabs-border-color: #{$tabs-border-color};
        --bs-nav-tabs-border-radius: #{$tabs-border-radius};
        --bs-nav-tabs-link-hover-border-color: #{$tabs-link-bg-color};
        --bs-nav-tabs-link-active-color: #000;
        padding-left: #{$tabs-border-radius};
        padding-right: #{$tabs-border-radius * 2.5};
        border-bottom-width: 0;
        gap: 4px;
        overflow-x: auto;
        overflow-y: hidden;
        width: 100%;
        display: flex;
        flex-wrap: nowrap;
        .nav-item{
            flex-shrink: 0;
        }
        .nav-link{
            --bs-nav-link-padding-y: 10px;
            --bs-nav-link-padding-x: .5rem;
            //
            color: $tabs-link-color;
            background-color: $tabs-link-bg-color;
            font-size: 1rem;
            border-bottom: 1px solid var(--bs-nav-tabs-border-color);
            @include breakpoint($lg){
                font-size: 1.125rem; //18px
            }
            &:hover{
                color: currentColor;
                border-bottom: 1px solid var(--bs-nav-tabs-border-color);
            }
            &.active{
                font-weight: bold;
                background-color: #fff;
                border-bottom-color: white;
                &:hover{
                    border-bottom-color: white;
                }
            }
        }
    }
    > .tab-content{
        padding: 16px;
        border: 1px solid $tabs-border-color;
        border-radius: 0 0 12px 12px;
        @include breakpoint($xl){
            padding: 30px;
            border-radius: 12px;
        }
    }
}

