/* Base */
html {
    &:has(.fontSize-lg:checked) {
        font-size: 1.25rem;
    }
    &:has(.fontSize-sm:checked) {
        font-size: 0.85rem;
    }
}
body {
    color: $body-text-color;
    background-color: $body-bg-color;
    font-family: Arial, 微軟正黑, sans-serif;
}
a {
    text-underline-offset: 3px;
}
/* wrapper */
.wrapper {
    .top {
        z-index: 1050;
    }
    // Bottom
    .bottom {
        padding-top: 58px;
        @include breakpoint($sm) {
            padding-top: 46px;
        }
        @include breakpoint($xl) {
            padding-top: 68px;
        }
        @include breakpoint($xxl) {
            padding-top: 72px;
        }
    }
    .sidebar {
        width: 305px;
        height: 100vh;
        overflow-x: hidden;
        overflow-y: auto;
        background-color: #fcfbfb;
        border-right: 1px solid #e1f3fb;
        transition: all 0.2s ease;
        position: fixed;
        left: 0;
        z-index: 1000;
        transform: translateX(-100%);
        @include breakpoint($lg) {
            transform: translateX(0);
        }
        &.active {
            transform: translateX(0);
        }
        &-toggler {
            width: 24px;
            height: 100px;
            top: 100px;
            left: 0;
            z-index: 999;
            position: fixed;
            border-radius: 0 0.5rem 0.5rem 0;
            transform: translateX(0);
            display: flex;
            flex-direction: column;
            transition: all 0.2s ease;
            @include breakpoint($lg) {
                left: -100% !important;
            }
            &.active {
                left: 305px;
                img {
                    transform: rotate(180deg);
                }
            }
            span {
                transform: rotate(90deg);
                color: $white;
                margin: 12px 0;
            }
            img {
                width: 12px;
                transition: all 0.2s ease;
            }
        }
    }

    /* 主內容區塊推開 sidebar */
    .main {
        display: block;
        width: 100%;
        padding-inline: 16px;
        padding-bottom: 40px;
        @include breakpoint($lg) {
            margin-left: 305px;
            width: calc(100% - 305px);
        }
        @include breakpoint($xl) {
            padding-inline: 32px;
        }
        @include breakpoint($xxl) {
            padding-inline: 40px;
        }
        @include breakpoint($xxxl) {
            padding-inline: 60px;
        }
        &-header{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            button{
                font-size: .9rem;
                padding: 6px 12px;
                border-radius: 4px;
                line-height: 1;
                 @include breakpoint($lg){
                    font-size: 1rem;
                    padding: 8px 12px;
                    min-width: 105px;
                    border-radius: 8px;
                 }
            }
        }
        &-title{
            font-size: 1.5rem;
            padding-left: 24px;
            @include breakpoint($lg){
                font-size: 2rem;
                padding-left: 0;
            }
        }
    }
    .content {
        max-width: Min(80rem, 1600px);
        margin: 0 auto;
    }
}
table[data-title="overline2"] {
    p {
        margin-bottom: 0;
        @include over-text(2);
    }
}
table[data-thead="none"] {
    border-color: $blue-light;
    min-height: 241px;
    @include breakpoint($xxxl) {
        min-height: 249px;
    }
    th {
        background: $blue-light !important;
        color: $black;
        border-color: $white;
        border-left: none;
        position: relative;
        &::after {
            content: "";
            width: 100%;
            height: 1px;
            position: absolute;
            left: 0;
            bottom: -1px;
            background: $white;
        }
    }
    td {
        border-right: none;
    }
    & ~ ul {
        font-size: 0.8rem;
        display: flex;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 0;
        padding: 8px 24px;
        @include breakpoint($md) {
            font-size: 1rem;
        }
        @include breakpoint($xxxl) {
            padding: 16px 24px;
            flex-direction: row;
            justify-content: center;
            align-items: center;
        }
        li {
            list-style: none;
            margin-right: 16px;
            @include breakpoint($xxxl) {
                margin-right: 40px;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
}
.info-list {
    margin-bottom: 0;
    padding-left: 0;
    li {
        list-style: none;
        display: flex;
        font-weight: $font-weight-medium;
        margin-bottom: 4px;
        &:last-child {
            margin-bottom: 0;
        }
        &::before {
            content: "";
            display: inline-block;
            width: 8px;
            height: 11px;
            background: $red;
            margin-right: 8px;
            margin-top: 5px;
        }
        p {
            margin-bottom: 0;
        }
    }
}
.base-container {
    background: $white;
    border: 1px solid $primary;
}
.form-label {
    flex-shrink: 0;
    margin-right: 12px;
}
.form-select {
    border-radius: 8px;
    background-position-y: 14px;
}
.data-box {
    border-radius: 10px;
    border: 2px solid;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px 16px 8px 16px;
    text-align: center;
    box-shadow: 0px 3px 6px $grey;
    cursor: pointer;
    position: relative;
    transition: all 0.2s ease;
    overflow: hidden;
    @include breakpoint($xxl) {
        padding: 24px 32px 8px 32px;
    }
    &:before {
        content: "";
        display: block;
        width: 100%;
        height: 8px;
        left: 0;
        top: 0;
        position: absolute;
        transition: all 0.2s ease;
        z-index: 50;
    }
    &.active {
        box-shadow: none;
        &::before {
            height: 57px;
        }
        .data-box-title {
            color: $white !important;
        }
    }
    &[data-style="red"] {
        border-color: $red;
        &::before {
            background: $red;
        }
        .data-box-title {
            color: $red;
            border-color: $red;
            &::before {
                background: $red;
            }
        }
    }
    &[data-style="yellow"] {
        border-color: $amber;
        &::before {
            background: $amber;
        }
        .data-box-title {
            color: $amber;
            border-color: $amber;
            &::before {
                background: $amber;
            }
        }
    }
    &[data-style="green"] {
        border-color: $green;
        &::before {
            transition: all 0.2s ease;
            background: $green;
        }
        .data-box-title {
            color: $green;
            border-color: $green;
            &::before {
                background: $green;
            }
        }
    }
    &-title {
        border-bottom: 1px solid;
        position: relative;
        padding-bottom: 8px;
        z-index: 100;
        font-size: $h6-font-size;
        @include breakpoint($xxl) {
            font-size: $h5-font-size;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            width: 12px;
            height: 12px;
            transform: rotate(45deg);
            left: 0;
            bottom: -6px;
            @include breakpoint($xxl) {
                width: 16px;
                height: 16px;
                bottom: -9px;
            }
        }
    }
    span {
        color: $dark-grey;
    }
}
.header-light {
    border-bottom: 6px solid;
    position: relative;
    padding-bottom: 4px;
    padding-left: 24px;
    margin-bottom: 0;
    &::before {
        content: "";
        display: block;
        position: absolute;
        width: 12px;
        height: 12px;
        transform: rotate(45deg);
        left: 0;
        top: 4px;
    }
    &[data-style="red"] {
        border-color: $red;
        color: $red;
        &::before {
            background: $red;
        }
    }
    &[data-style="yellow"] {
        border-color: $orange;
        color: $orange;
        &::before {
            background: $orange;
        }
    }
    &[data-style="green"] {
        border-color: $green;
        color: $green;
        &::before {
            background: $green;
        }
    }
}
thead.sticky-top {
    &::before {
        content: "";
        width: 1px;
        height: 100%;
        background: $primary;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 100;
    }
    &::after {
        content: "";
        width: 100%;
        height: 1px;
        background: $primary;
        position: absolute;
        left: 0;
        bottom: 1px;
    }
    th {
        position: relative;
        &:before {
            content: "";
            width: 100%;
            height: 1px;
            background: $primary;
            position: absolute;
            left: 0;
            top: -1px;
            z-index: 50;
        }
        &:after {
            content: "";
            width: 1px;
            height: 100%;
            background: $teal-light-pale;
            position: absolute;
            right: -1px;
            top: 0;
            z-index: 50;
        }
    }
}
table[data-style="pale"] {
    thead {
        th {
            background: $sky-blue-light;
            color: $body-text-color;
            border: 1px solid $teal-pale;
            font-size: 1rem;
        }
    }
}
[data-bs-toggle="tooltip"] {
    border: none !important;
    outline: none !important;
    &:hover,
    &:active {
        img {
            filter: invert(46%) sepia(93%) saturate(4919%) hue-rotate(159deg) brightness(96%) contrast(98%);
        }
    }
}
.custom-tooltip {
    background: $white;
    color: $black;
    --bs-tooltip-bg: $white;
    --bs-tooltip-color: $black;
    border: 1px solid $primary;
    border-radius: 4px !important;
    font-size: 1rem;
    font-weight: $font-weight-medium;
    padding: 4px 8px;
    opacity: 1 !important;
    margin-left: -10px !important;
    margin-top: 16px !important;
}
.tooltip-arrow {
    display: none !important;
}
.light-container {
    background: $white;
    border: 1px solid $tabs-border-color;
    border-radius: 6px;
    p {
        font-size: $h6-font-size;
        @include breakpoint($xl) {
            font-size: $h5-font-size;
        }
    }
    &[data-con="number"] {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        @include breakpoint($sm) {
            justify-content: center;
        }
        p {
            flex-shrink: 0;
        }
        span {
            display: inline-block;
            width: 100%;
            flex-shrink: 0;
            @include breakpoint($sm) {
                width: auto;
            }
        }
        button {
            margin-right: -12px;
            flex-shrink: 0;
            @include breakpoint($sm) {
                margin-right: 0;
            }
        }
    }
}
.chart-header {
    border-radius: 10px 10px 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 24px;
    p {
        color: $white;
        font-size: 1.2rem;
        flex-shrink: 0;
        margin-bottom: 0;
    }
    .form-select {
        width: 160px;
        padding: 4px 12px;
    }
}
.table-rwd {
    min-width: 100%;
    &>thead>tr>th,&>tbody>tr>th {
        display: none;
        @include breakpoint($md) {
            display: table-cell;
        }
    }
    &>tbody.text-center>tr>td{
        text-align: center;
    }
    &>tbody>tr>td {
        display: flex;
        text-align: left;
        align-items: start;
        padding: 0.5rem;
        @include breakpoint($md){
            text-align: center;
        }
        &:first-child {
            padding-top: 0.5rem;
            color: $teal;
        }
        &:last-child {
            padding: 0.2rem 0.5rem 0.5rem 0.5rem;
            @include breakpoint($md){
                padding: 0.5rem 0.5rem;
            }
        }
        @include breakpoint($md) {
            display: table-cell;
            padding: 0.5rem 0.5rem;
        }
        &:before {
            content: attr(data-th) "：";
            display: inline-block;
            padding-left: 8px;
            color: $teal;
            font-weight: $font-weight-medium;
            border-radius: 4px;
            flex-shrink: 0;
            min-width: 84px;
            @include breakpoint($md) {
                display: none;
            }
        }
        &[data-require="true"]{
            position: relative;
            &:before{
                padding-left: 16px !important;
            }
            &:after{
                content: "*";
                color: $red;
                position: absolute;
                left: 8px;
                top: 4px;
                @include breakpoint($md) {
                display: none;
            }
            }
        }
    }
    &>tbody[data-con="form"]>tr>td{
        align-items: center;
    }
    &>tbody[data-title-align="right"]>tr>td{
        &::before{
            text-align: right;
        }
    }
    &[data-th="none"]{
        &>tbody>tr>td{
            &::before{
                display: none;
            }
        }
    }
    &[data-style="multi"]{
        &>tbody>tr>td{
            flex-direction: column;
            padding-top: 0;
            vertical-align: middle;
            @include breakpoint($md){
                padding-top: 8px;
            }
            &:before{
                width: calc(100% + 16px);
                background: $blue-light;
                color: $black;
                margin: 0 -8px 8px -8px;
                border-radius: 0;
                padding: 4px 8px;
                @include breakpoint($md){
                    display: none;
                }
            }
        }
    }
    &[data-style="cost"] {
        font-size: 1rem;
        @include breakpoint($md) {
            font-size: 1.1rem;
        }
        tbody th {
            font-weight: $font-weight-medium;
            display: block;
            width: 100%;
            background: $primary;
            color: $white;
            @include breakpoint($md) {
                display: table-cell;
                background: $white;
                color: $black;
                width: auto;
            }
        }

        td {
            text-align: end;
            &::before {
                min-width: 96px;
                @include breakpoint($md) {
                    display: none;
                }
            }
        }
        th,
        td {
            padding: 4px;
            @include breakpoint($md) {
                padding: 12px;
            }
            &:nth-child(2) {
                padding-top: 12px;
            }
            &:last-child {
                padding-bottom: 12px;
            }
        }
    }
}
.table-striped[data-style="cost"] > tbody > tr:nth-of-type(odd) > th {
    --bs-table-bg-type: $primary;
    --bs-table-color-type: $white;
}
.sum {
    display: flex;
    justify-content: flex-start;
    padding-left: 0;
    flex-wrap: wrap;
    @include breakpoint($xxxl) {
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    &-item {
        list-style: none;
        width: calc(100% - 40px);
        display: flex;
        justify-content: start;
        margin-bottom: 12px;
        @include breakpoint($sm) {
            width: auto;
            justify-content: center;
        }
        @include breakpoint($xxxl) {
            width: 100%;
            margin-bottom: 0;
        }
        &:nth-child(even) {
            padding: 0 12px;
            display: flex;
            align-items: center;
            width: 40px;
        }
    }
    &-container {
        background: $white;
        border-radius: 8px;
        padding: 8px 12px;
        display: flex;
        align-items: center;
        justify-content: start;
        width: 100%;
        @include breakpoint($sm) {
            flex-direction: column;
            align-items: center;
        }
        @include breakpoint($xxl) {
            padding: 12px 16px;
        }
        @include breakpoint($xxxl) {
            padding: 16px 24px;
        }
        span {
            margin-left: 12px;
            @include breakpoint($sm) {
                margin-left: 0;
            }
        }
    }
    &-title {
        margin-bottom: 0;
        font-size: 1rem;
        width: 90px;
        text-align: end;
        @include breakpoint($sm){
            width: auto;    
            text-align: start;
        }
        @include breakpoint($lg) {
            font-size: 1.2rem;
        }
    }
}
.notice{
    font-size: 0.9rem;
    padding-left: 0;
    @include breakpoint($md){
        font-size: 1rem;
    }
    &>li{
        list-style: none;
        display: flex;
        &::before{
            content: "※";
        }
    }
}
.nowrap{
    white-space: nowrap;
}