.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $primary;
    color: white;
    padding: 6px 16px;
    @include breakpoint($xl) {
        padding: 16px 32px;
    }
    @include breakpoint($xxl) {
        padding: 18px 40px;
    }
    @include breakpoint($xxxl) {
        padding: 18px 60px;
    }
    &-end {
        flex-shrink: 0;
        font-size: .9rem;
        @include breakpoint($xxl) {
        font-size: 1rem;
    }
    }
    a {
        color: currentColor;
        text-decoration: none;

    }
}
/* Brand name */
.brand-name {
    margin-bottom: 0;
    font-weight: bold;
    font-size: 1.2rem;
    @include breakpoint($sm) {
        font-size: 1.5rem;
    }
    @include breakpoint($xl) {
        font-size: 1.875rem;
    }
}

.font-size-wrapper {
    display: none;
    @include breakpoint($md) {
        display: flex;
    }
}
