/* nav-pills */
.nav-pills{
    gap: .5rem;
    @include breakpoint($lg){
        gap: 1rem;
    }
    .nav-item{
        // flex: 0 0 150px;
        // flex-basis: 150px;
        min-width: auto;
        @include breakpoint($lg){
            min-width: 140px;
        }
    }
    .nav-link{
        --bs-nav-link-padding-y: 6px;
        //--
        border: 2px solid $teal;
        color: $teal;
        background-color: white;
        border-radius: 10px;
        font-weight: bold;
        font-size: 1rem;
        text-align: center;
@include breakpoint($lg){
    font-size: 1.125rem; //18px
}
        &.active{
            color: white;
            background-color: $teal;
        }
    }
}