/* state-text */
.state-text{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2px;
    font-size: .9rem;
    white-space: nowrap;

    &.success{
        color: $success;
    }
    &.danger{
        color: $danger;
    }
    &.warning{
        color: $orange;
    }
    span{
        margin-top: 3px;
        @include breakpoint($md){
            margin-top: 0;
        }
    }
}