.menu{
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 1.125rem; //18px

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }
    >li{
        border-bottom: 1px solid $menu-primary;
    }
    a{
        display: flex;
        align-items: center;
        color: $menu-submenu-text-color;
        text-decoration: none;
    }

    // Children
    .children{
        padding-left: 2.5rem; //40px
        padding-right: 1.25rem; //20px
        padding-block: 1rem;
        justify-content: space-between;    
        color: $menu-primary;   
        >span{
            display: flex;
            align-items: center;
            gap: 1rem
        }
        .text{
            color: black;
            font-weight: bold;
        }
    }
    // Children箭頭
    .children-arrow{
        display: inline-block;
        // transform: rotate(-180deg);
        // transition: all .2s;
        > *{
            display: none;
        }
        .expanded-false{
            display: inline;
        }
       
    }
    // Level-2
    .level-2{
        display: none;
        padding: 16px 24px;
        background-color: $menu-submenu-bg-color;
        border-top: 1px solid $menu-primary; 

        a{
            height: 44px;
            padding-left: 24px;
            background-color: transparent;
            border-radius: 10px;
            transition: all .2s;

            &:hover{
                background-color: rgba($menu-primary, .2);
            }
        }
    }
    
    // 狀態 active
    >li:has(.active){
        .children .text{
            color: $menu-active;
        }
        a.active{            
            color: white;
            background-color: $menu-primary;
        }

        // icon
        .menu-system{
            background-image: url(../images/menu_1_system-active.svg);
        }
        .menu-office{
            background-image: url(../images/menu_2_office-active.svg);
        }
        .menu-plan{
            background-image: url(../images/menu_3_plan-active.svg);
        }
        .menu-money{
            background-image: url(../images/menu_4_money-active.svg);
        }
        .menu-result{
            background-image: url(../images/menu_5_result-active.svg);
        }
        .menu-member{
            background-image: url(../images/menu_6_member-active.svg);
        }
    }
    // 狀態 show
    .children.show{
        .children-arrow{
            // transform: rotate(0deg);
            .expanded-false{
                display: none;
            }
            .expanded-true{
                display: inline;
            }
        }

        & + .level-2{
            display: block;
        }
    }
}