::root{
    --bs-backdrop-opacity: .15;
}
.modal{
    --bs-modal-header-padding: 1.5rem 1.5rem 0rem;
    --bs-modal-header-border-width: 0;
    --bs-modal-footer-padding: 0rem 1.5rem 1.5rem;
    --bs-modal-footer-border-width: 0;
    --bs-modal-footer-gap: 20px;
    --bs-modal-padding: 1.5rem;
    --bs-modal-border-width: 0;
    --bs-modal-border-radius: 12px;
    &-content{
        border-top: 12px solid $primary;
    }
    &-backdrop{
        // --bs-backdrop-opacity: 0.15;
    }
    &-dialog{
        max-width: 90%;
        @include breakpoint($xl){
            max-width: 1140px;
        }
    }
}