/* columns */
.grid-columns-two{
    grid-template-columns: 1fr 1fr;
}
.grid-full{
    grid-column: 1 / -1;
}
/* img */
.img-container{
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
/* grid left auto */
.grid-left-auto{
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: start;

    &[role="list"]{
        .start{
            display: flex;
            // align-items: center;            
            &::before{
                content: '';
                flex-shrink: 0;
                display: block;
                width: .65rem;
                height: .65rem;
                margin: .5em;
                border-radius: 999rem;
                background-color: #B6D0DD;
            }
        }
    }
}
/* flex-center */
.flex-center{
    display: flex;
    align-items: center;
    justify-content: center;
}
/* flex-start-end */
.flex-start-end{
    display: flex;
    justify-content: space-between;
}
/* object-fit-cover */
.img-object-fit-cover{
    video,
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
/* object-fit-contain */
.img-object-fit-contain{
    video,
    img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}
/* line-clamp */
.line-clamp {
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
  