/* table */
.table{
    --bs-table-border-color: #{$table-border-color};
    //--
    th, td{
        vertical-align: middle;
    }
    th{
        text-align: center;
        font-weight: 500;
    }
    thead{
        th{
            --bs-table-bg: #{$primary};
            //--
            border-color: #CEF6F1;
            color: white;
            font-size: 1.125rem; //18px
        }
    }

    // Bg color
    &.table-teal-light{
        > * > * > th{
            --bs-table-bg: #{$teal-light};
            --bs-table-striped-bg: #{$teal-light};
            --bs-table-border-color: white;
            border-color: var(--bs-table-border-color);
            border-top: 1px solid var(--bs-table-border-color);
            color: currentColor;
            font-weight: normal;

            &:first-child{
                border-left-color: $blue-light;
            }
            &:last-child{
                border-right-color: $blue-light;
            }
        }
        
    }
    &.table-blue-light{
        // --bs-table-border-color: #CEF6F1;
        --bs-table-border-color: #{$teal-pale};
        --bs-table-bg: #FCFBFB;
        > *> * > th{
            --bs-table-bg: #C4E2FF;
            --bs-table-striped-bg: #{$blue-light};
            --bs-table-border-color: white;
            border-color: var(--bs-table-border-color);
            border-top: 1px solid var(--bs-table-border-color);
            color: currentColor;
            font-weight: normal;

            // border-left-color: var(--bs-table-bg);
            // border-right-color: var(--bs-table-bg);
            &:first-child{
                border-left-color: var(--bs-table-bg);
                // border-left-color: $primary;
            }
            &:last-child{
                border-right-color: var(--bs-table-bg);
                // border-right-color: $primary;
            }
        }
        thead th{
            border-color: white;
        }
    }
    tr.table-teal-light{
        --bs-table-bg: #{$teal-light};
        // --bs-table-border-color: white;
        border-color: var(--bs-table-border-color);
        > *{
            &:first-child{
                border-left-color: $teal-light;
            }
            &:last-child{
                border-right-color: $teal-light;
            }
        }
    }
    tr.table-blue-light{
        --bs-table-bg: #{$blue-light};
        --bs-table-border-color: white;
        border-color: var(--bs-table-border-color);
        > *{
            &:first-child{
                border-left-color: $blue-light;
            }
            &:last-child{
                border-right-color: $blue-light;
            }
        }
    }
    tr.table-blue-light-white{
        --bs-table-bg: #{$blue-light-white};
    }
    tr.table-grey-light-darken{
        --bs-table-bg: #{$grey-light-darken};
    }
}

// outline
.table-outline{
    thead{
        th{
            border-top: 1px solid $primary;
            border-bottom: 1px solid $primary;
        }
    }
    th:first-child,
    td:first-child{
        border-left-color: $primary;
    }
    th:last-child,
    td:last-child{
        border-right-color: $primary;
    }
    tbody{
        tr:first-child > *{
            border-top: 1px solid $primary;
        }
        tr:last-child > *{
            border-bottom: 1px solid $primary;
        }
    }
}


/* table-striped */
.table-striped{
    --bs-table-bg: #{$table-striped-bg};
    --bs-table-striped-bg: #fff;

    td.bg-green-light{
        // background:  #000 !important;
    }
    &>tbody>tr:nth-of-type(odd)>*.bg {
        // background-color: inherit;
        --bs-table-bg-type: unset;
    }
}