.breadcrumb{
    --bs-breadcrumb-padding-x: 2rem;
    --bs-breadcrumb-padding-y: 0rem;
    --bs-breadcrumb-item-padding-x: 1rem;
    --bs-breadcrumb-margin-bottom:0;
    --bs-breadcrumb-bg: #{$breadcrumb-bg-color};
    --bs-breadcrumb-border-radius: 0;
    --bs-breadcrumb-divider: '\f105';
    --bs-breadcrumb-item-active-color: #{$breadcrumb-active-color};
    --bs-breadcrumb-divider-color: #{$breadcrumb-text-color};
    //--
    z-index: 10;
    align-items: center;
    justify-content: flex-end;
    min-height: 44px;
    color: $breadcrumb-text-color;
    border-bottom: 1px solid #e4e7ee;
    margin-bottom: 40px;
    margin-inline: -16px;
    padding: 8px 16px;
    font-size: .9rem;
    @include breakpoint($xl) {
            margin-inline: -32px;
            font-size: 1rem;
            padding: 12px 32px;
        }
        @include breakpoint($xxl){
            margin-inline: -40px;
            padding: 12px 40px;
        }
        @include breakpoint($xxxl){
            margin-inline: -60px;
            padding: 12px 60px;
        }
    a{
        color: currentColor;
    }
}
.breadcrumb-item{
    display: flex;
    align-items: center;
    line-height: 1.3;
}
.breadcrumb-item+.breadcrumb-item{
    padding-left: 0;
}
.breadcrumb-item+.breadcrumb-item::before{
    font-weight: 700;
    font-family: $font-awesone;
    font-size: .75rem;
    float: none;
    padding-inline: 8px;
    @include breakpoint($xl){
        padding-inline: 12px;
    }
    @include breakpoint($xxl){
        padding-inline: 16px;
    }
}