// 斷點
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
  xxxl: 1920px,
);
$sm: map-get($grid-breakpoints, 'sm');
$md: map-get($grid-breakpoints, 'md');
$lg: map-get($grid-breakpoints, 'lg');
$xl: map-get($grid-breakpoints, 'xl');
$xxl: map-get($grid-breakpoints, 'xxl');
$xxxl: map-get($grid-breakpoints, 'xxxl');

// Colors
$blue: #256CBE;
$sky-blue: #509cf3;
// $sky-blue-light: #e1f3fb;
$sky-blue-light: #E8F6FF;
// $teal: #0db6c9;
$teal: #039595;
$teal-pale: #4DC9C6;
$orange: #f97c0e;
$amber: #ffab1a;
$yellow: #ffd91a;
$highlight: #FFFF00;
$green: #24ae15;
$red: #ff6b6b;
$orange-red: #FF4040;
$dark-grey: #505050;
$grey: #999999;
$grey-pale: #C8C8C8;
$grey-light: #FCFBFB;
$grey-light-darken: #F8F8F8;
$purple: #9B95EB;
$purple-dark: #B477F9;
$purple-xdark: #927FE2;

$blue-light-white: #F7FBFD;
// $blue-light: #c1deff;
$blue-light: #C4E2FF;
$blue-gray: #8FB4D8;
$teal-light: #b6ece5;
$teal-light-pale: #CEF6F1;
// $green-light: rgba($green, .2);
// $orange-light: rgba($amber, .2);
// $red-light: rgba($red, .2);
$green-light: #d3efcf;
$orange-light: #ffeed1;
$red-light: #ffe1e1;

$primary: $teal-pale;
$warning: $orange;
$success: $green;
$danger: $red;
$info: $teal-pale;
$dark: $dark-grey;
$secondary: $grey;
$disabled: #e4e7ee;
$border: $disabled;
$placeholder: $grey;

$body-text-color: #000;
$body-bg-color: #fff;

$text-grey: $grey;

// Menu
$menu-primary: $primary;
$menu-submenu-text-color: $dark-grey;
$menu-submenu-bg-color: #E1FBFB;
$menu-active: $blue;

// Breadcrumb
$breadcrumb-text-color: #666666;
$breadcrumb-bg-color: #E1FBFB;
$breadcrumb-active-color: $primary;

// Form
$form-border-color: $grey;
$form-disabled-border-color: $disabled;
$form-disabled-bg-color: $disabled;

// Tabs
$tabs-link-color: $dark-grey;
$tabs-link-bg-color: #E1FBFB;
$tabs-border-color: #ced3d5;
$tabs-border-radius: 12px;

// Table
$table-border-color: $primary;
$table-striped-bg: #F8F8F8;



$font-awesone: "Font Awesome 6 Free";