/* pagination */
.pagination{
    --bs-pagination-padding-x: 4px;
    --bs-pagination-padding-y: 4px;
    --bs-pagination-color: #{$primary};
    --bs-pagination-border-color: #{$primary};
    --bs-pagination-border-radius: 7px;
    --bs-pagination-hover-bg: var(--bs-tertiary-bg);
    --bs-pagination-hover-color: #{$primary};
    --bs-pagination-hover-border-color:  #{$primary};
    --bs-pagination-active-color: #{$teal};
    --bs-pagination-active-bg: white;
    --bs-pagination-active-border-color: #{$primary};
    //--
    gap: 8px;
    
}
/* page-link */
.page-link{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 26px;
    line-height: 1;
    border-radius: var(--bs-pagination-border-radius) ;
   
    &:has(.icon){
        --bs-pagination-color: #{$teal};
        --bs-pagination-border-color: #d5d5d5;
        --bs-pagination-border-radius: 999rem;
        --bs-pagination-hover-color: #{$teal};
        --bs-pagination-hover-border-color: #{$teal};
    }
}
.active>.page-link{
    font-weight: bold;
}