/* scroll */
.scroll{   
    // overflow-y: auto;
    // overflow-x: hidden;
    overflow: auto;
    overscroll-behavior: contain;

    
    &::-webkit-scrollbar {
      width: 6px;
      height: 4px;
    }
    

    &::-webkit-scrollbar-track {
        background-color: $blue-light;
        transition: all .5s;
    }
   
    
    &::-webkit-scrollbar-thumb {
        background-color: $blue-gray;
        background-clip: content-box;
    }

    &:hover{
        &::-webkit-scrollbar-track {
            background-color: $blue-light;
        }
    }
}