/* form-label */
.form-label{
    line-height: 1;
}
/* form-select */
.form-select{
    --bs-form-select-bg-img: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='8' height='6' viewBox='0 0 16 12'%3e%3cpolygon points='2,0 14,0 8,9' fill='%23039595'/%3e%3c/svg%3e");
}
/* form-control */
.form-control{
    &::placeholder{
        color: $placeholder;
    }
}
/* 共同 */
.form-select,
.form-control{
    --bs-border-color: #{$form-border-color};
    --bs-border-radius: 10px;

    // Disabled
    &:disabled{
        --bs-border-color: #{$form-disabled-border-color};
        background-color: $form-disabled-bg-color;
    }
}
/* date */
.form-control.date{
    --form-date-bg-img: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='25.152' height='25.152' viewBox='0 0 25.152 25.152'%3e%3cg id='common_ic_calendar' transform='translate(0)'%3e%3cpath d='M0,0H25.152V25.152H0Z' transform='translate(0)' fill='none'/%3e%3cpath d='M20.521,3.079H19.492V1H17.434V3.079H7.145V1H5.087V3.079H4.058A2.074,2.074,0,0,0,2,5.157V21.787a2.074,2.074,0,0,0,2.058,2.079H20.521a2.074,2.074,0,0,0,2.058-2.079V5.157A2.074,2.074,0,0,0,20.521,3.079Zm0,18.708H4.058V10.354H20.521Zm0-13.511H4.058V5.157H20.521Z' transform='translate(0.286 0.143)' fill='%23039595'/%3e%3c/g%3e%3c/svg%3e");
    padding-right: 2.25rem;
    background-image: var(--form-date-bg-img);
    background-repeat: no-repeat;
    background-position: right .5rem center;
}
/* form-check-input */
.form-check-input{
    --bs-border-color: #{$form-border-color};
    //--
    font-size: 1.125rem; //18px

    &:checked{
        background-color: $teal;
        border-color: $teal;
    }
}
.form-check-input:checked[type="radio"]{
    --bs-form-check-bg-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='2' fill='%23039595'/%3e%3c/svg%3e");
    background-size: 24px;
    background-color: white;
    border-color: $form-border-color;

}
/* form-check-label */
.form-check-label{
    font-size: 1.125rem; //18px
}

