/* state-ball */
.state-ball{
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 999rem;

    &.success{
        background-color: $success;
    }
    &.warning{
        background-color: $amber;
    }
    &.danger{
        background-color: $danger;
    }
}