/* collapse-icon */
.collapse-icon{
    color: currentColor;
    font-size: 1.5rem; //24px
    
    > *{
        display: none;
    }
    .expanded-false{
        display: inline;
    }
    &[aria-expanded="true"]{
        .expanded-false{
            display: none;
        }
        .expanded-true{
            display: inline;
        }
    }
}