/* block */
.block{
    display: flex;
    flex-direction: column;
    padding: 16px;
    background: #FCFBFB ;
    border: 1px solid #E4E7EE;
    border-radius: 10px;
    @include breakpoint($lg){
        padding: 30px;
        padding-top: 16px;
    }
    &-head{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 10px;
        width: 100%;
    }
    
    .block-head{
        --bs-border-color: #{$grey};
        &-title{
            font-size: $h5-font-size;
            @include breakpoint($xxl){
                font-size: $h4-font-size;
            }
        }
    }
}