.goTop {
    //--
    position: fixed;
    right: 0;
    bottom: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 44px;
    padding-top: 22px;
    color: white;
    background: $teal url(../images/common_ic_top.svg) no-repeat center 8px ;
    background-size: 8px;
    border: 2px solid white !important;
    border-right-width: 0;
    font-size: 1rem;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 12px 0px 0px 12px;
    text-decoration: none;
    font-weight: bold;
    z-index: 1050;
    @include breakpoint($md) {
        bottom: 130px;
        width: 96px;
        height: 70px;
        background: $teal url(../images/common_ic_top.svg) no-repeat center 12px;
        background-size: 16px;
        border-radius: 20px 0px 0px 20px;
        padding-top: 30px;
        font-size: 1.375rem; //22px
    }
}
