.font-size-filter{
    label{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 1.75rem; //28px
        height: 1.75rem; //28px
        font-size: 1.125rem; //18px
        font-weight: 500;
        color: black;
        border: 1px solid #039595;
        background-color: white;
        border-radius: 5px;
        line-height: 1;
    }    
    input:checked + label{
        color: white;
        background-color: $blue;
    }
}