.tablesort-header-inner{
    &::after{
        font-family: $font-awesone;
        font-weight: 700;
        display: inline;
        margin-left: 4px;
    }
}
.tablesort-header{
    cursor: pointer;

    // 無排序
    &-unSorted{
        .tablesort-header-inner::after{
            content: '\f0dc';
        }
    }
    // 上升
    &-asc{
        .tablesort-header-inner::after{
            content: '\f0d8';
        }
    }
    // 下降
    &-desc{
        .tablesort-header-inner::after{
            content: '\f0d7';
        }
    }
}
