/* block-bar */
.block-bar{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 46px;
    padding: 8px 16px 8px 24px;
    background: $primary;
    color: white;
    line-height: 1;

    &-title{
        font-size: 1.125rem; //18px
        font-weight: bold;
        margin-bottom: 0;
    }

    > .start,
    > .end{
        display: flex;  
        align-items: center;
        gap: 8px;
    }
}