/* btn-min-width */
.btn-min-width{
    min-width: 105px;
}
/* btn-file */
.btn-file{
    border-radius: 0;
    border-bottom: 1px solid $primary;
}
/* btn-icon */
.btn-icon{
    --w: 24px;
    //--
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: var(--w);
    height: var(--w);

    background: none;
    border-width: 0;
    font-size: 1.25rem; //20px;
    font-weight: normal;
}