/* card */
.card{
    --bs-card-border-radius: 10px;
    --bs-card-inner-border-radius: 9px;
    --bs-card-border-color: #{$border};
    --bs-card-cap-padding-y: 16px;
    //--
    
    &-header{
        color: white;
        text-align: center;
        line-height: 1;
        
    }
    &-body{
        box-shadow: inset 0 0 1px var(--bs-border-color);
        border-radius: 0 0 var(--bs-card-border-radius) var(--bs-card-border-radius);
    }
}


