/* number-card */
.number-card{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 54px;

    padding-inline: 18px;
    font-size: 1.125rem; //18px
    border-radius: 5px;
    border: 1px solid $border;
    background-color: white;

    &.bg-sky-blue-light{
        border-color: $sky-blue-light !important;
    }
}