/* == Utilities == */
// 文字顏色
$text-colors: (
    grey: $grey,
    yellow: $yellow,
    amber: $amber,
    highlight: $highlight,
    info: $info,
    danger: $orange-red,
    warning: $warning,
    success: $success,
    primary: $teal,
    grey-pale: $grey-pale,
    blue: $blue,
    green: $green,
    dark-grey: $dark-grey,
    teal: $teal,
    );
// 背景顏色
$bg-colors: ( 
    primary: $teal,
    info: $info,
    danger: $danger,
    warning: $amber,
    success: $success,
    purple: $purple-dark,
    blue: $blue,
    sky-blue-light: $sky-blue-light,
    blue-light: $blue-light,
    blue-light-white: $blue-light-white,
    teal-light: $teal-light,
    green-light: $green-light,
    orange-light: $orange-light,
    red-light: $red-light,
    grey-light: $grey-light,
    teal: $teal,
    purple-xdark: $purple-xdark,
);
// 字級
$font-sizes: (  
    15: .9375rem, //15px
    18: 1.125rem, //18px
);
// 寬度
$w-sizes: (  
    105: 105px,
);

$utilities: (
    "text-color": (
        class: text,
        property: color,
        values: $text-colors
    ),
    "bg-color": (
        class: bg,
        property: background-color,
        values: $bg-colors
    ),
    "font-sizes": (
        class: fs,
        property: font-size,
        values: $font-sizes
    ),
    "w-sizes": (
        class: min-w,
        property: min-width,
        values: $w-sizes
    ),
);

@import "../vendor/bootstrap-5.3.3/scss/utilities/api";