/* icon */
.ic{
    --w: 24px;
    //
    display: inline-block;
    width: var(--w);
    height: var(--w);
    background: no-repeat center;
    background-size: contain;

    //circle-check
    &.circle-check{
        background-image: url(../images/ic-circle-check.svg);
    }
    //xmark-red
    &.xmark-red{
        background-image: url(../images/ic-xmark-red.svg);
    }
    //xmark-grey
    &.xmark-grey{
        --w: 14px;
        background-image: url(../images/ic-xmark-grey.svg);
        background-size: 22px;
    }
    //xmark-teal
    &.xmark-teal{
        background-image: url(../images/ic-xmark-teal.svg);
    }
    //trash-can
    &.trash-can{
        --w: 14px;
        background-image: url(../images/ic-trash-can.svg);
    }
    //lock
    &.lock{
        --w: 14px;
        background-image: url(../images/ic-lock.svg);
    }
    //pen-to-square
    &.pen-to-square{
        --w: 14px;
        background-image: url(../images/ic-pen-to-square.svg);
    }
    //circle-question
    &.circle-question{
        background-image: url(../images/ic-circle-question.svg);
    }
    //circle-minus
    &.circle-minus{
        background-image: url(../images/ic-circle-minus.svg);
    }
    //circle-plus
    &.circle-plus{
        background-image: url(../images/ic-circle-plus.svg);
    }
    //plus
    &.plus{
        --w: 14px;
        background-image: url(../images/ic-plus.svg);
    }
    //cloud-arrow-up-white
    &.cloud-arrow-up-white{
        --w: 26px;
        background-image: url(../images/ic-cloud-arrow-up-white.svg);
    }
    //cloud-arrow-up-grey
    &.cloud-arrow-up-grey{
        --w: 26px;
        background-image: url(../images/ic-cloud-arrow-up-grey.svg);
    }
}

/* menu icon */
.menu{
    //system
    &-system{
        --w: 1.5rem;
        background-image: url(../images/menu_1_system.svg);
    }
    //office
    &-office{
        --w: 1.5rem;
        background-image: url(../images/menu_2_office.svg);
    }
    //plan
    &-plan{
        --w: 1.5rem;
        background-image: url(../images/menu_3_plan.svg);
    }
    //money
    &-money{
        --w: 1.5rem;
        background-image: url(../images/menu_4_money.svg);
    }
    //result
    &-result{
        --w: 1.5rem;
        background-image: url(../images/menu_5_result.svg);
    }
    //member
    &-member{
        --w: 1.5rem;
        background-image: url(../images/menu_6_member.svg);
    }
}

/* ic-file */
.ic-file{
    --w: 36px;
    //-
    flex-shrink: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: var(--w);
    height: var(--w);


    font-size: .75rem; //12px
    background-color: white;
    border-radius: 999rem;
    border: 2px solid currentColor;
    line-height: 1;
    font-weight: bold;
    font-style: normal;
    font-family: Arial, Helvetica, sans-serif;

    &.text-danger{
        color: $danger !important;
    }
}